










































import {
  Component,
  Inject,
  Provide,
  ProvideReactive,
  Vue,
  Watch,
} from "vue-property-decorator";
import ContainerBoxLeft from "@/components/Home/ContainerBoxLeft.vue";
import {
  NoteDataType,
  user
} from "@/typs";
import Nav from "@/components/Nav.vue";
import {
  mapGetters
} from "vuex";

@Component({
  components: {
    Nav,
    ContainerBoxLeft,
  },
})
export default class Home extends Vue {

  user: user = {
    id: 0,
    username: "",
    nickname: "",
    avatar: "",
    email: "",
  };
  // todo 刷新当前组件
  slideShow: boolean = false;
  routeId!: number;
  formData: any | undefined;
  previewAvatar: string = "";
  keyword:string='';
  uploadFile:any;
  // @ProvideReactive() keyword: string = ''
  public $refs!:{
    uploadImg:HTMLInputElement
  }
  mounted(){
    const preview = document.querySelector('.preview');
    this.$refs.uploadImg.style.display = 'none';
  }


  //获取用户信息
  getUser(userData: user) {
    this.user = userData;
    this.previewAvatar = this.user.avatar;
    //@ts-ignore
    this.$refs.userInfo.open();
  }

  //关闭弹窗时释放预览图
  revokePreview(){
      URL.revokeObjectURL(this.uploadFile)
  }
  //上传图片
  handleFiles() {
    //@ts-ignore
    this.uploadFile = this.$refs.uploadImg.files[0];
    //图片不能过大
    let number = this.uploadFile.size;
    if (number > 1048576 * 3) {
      this.$toast.error("图片大小不能超过3MB");
      return;
    }
    this.previewAvatar = URL.createObjectURL(this.uploadFile); //缩略图
    this.formData = new FormData();
    this.formData.append("img", this.uploadFile);
  }
  submit() {
    this.axios
      .post("/avatarUpload", this.formData)
      .then((res) => {
        if (res.data.statusCode === 0) {
          this.user.avatar =
            "http://qiniu.dreamsakula.top/" +
            res.data.respBody.key +
            `?${Math.random() * 314}`;
        }
        this.axios.patch("/users", this.user).then((res) => {
          //@ts-ignore
          this.$refs.submited.open();
        });
      })
      .catch((error) => {
        throw error;
      });
  }

  // @Watch("$route")
  // routeChange(to: any, from: any) {
  //   this.keyword = "";
  // }
}
